@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  overflow-x: hidden;
}

html {
  background-color: rgb(22, 28, 30);
  margin: 0;
  padding: 0;
}

.navLinks {
  padding: 25px 25px;
  transition: color .3s;
}


@media screen and (max-width: 800px) {
  .navLinks {
    font-size: small;
  }
}


.navLinks:hover {
  color:rgb(228, 234, 233)
}

.outerWrapper1 {
  text-align: left;
  margin: 200px;
  height: 100vh;
}

.helloHeader {
  position: relative;
  height: 0px;
  color: rgb(3, 238, 172);
  font-size: 2.5vmin;
  width: 40vw;
}
.nameHeader {
}


.nameHeader2 {
  color: rgb(200, 200, 188);
  position: relative;
  left: 40px;
  font-size: 6.5vmin;
  width: 50vw;
}

.welcomeDesc {
}

@media screen and (max-width: 800px) {
  .welcomeDesc {
    width: 23rem
  }
}

.aboutMe {
  height: 100vh;
  width: 80vw;
}

.aboutHeader {
  color: rgb(3, 238, 172);
  font-size: 3.5vmin;
  /* margin: 40px; */
  position: relative;
  left: 12rem;
  /* text-align: center; */
}

.aboutBody {
  color: rgb(213, 242, 233);
  position: relative;
  left: 12rem;
  font-size: 1.1rem;
  width: 35rem;
}


.backTopButton {
  position: fixed;
  bottom: 15px;
  right: 20px;
}

.backTopStyle {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 1px solid black;
  background-color: rgb(3, 238, 172);
  color:rgb(22, 28, 30);
  cursor: pointer;
  background-size: contain;
}

.grow {
  transition: all .2s ease-in-out;
}

.grow:hover {
  transform: scale(1.3);
}

.profilePic {
  height: 27rem;
  width: 13rem;
  /* border: 1px solid white; */
  background-size: cover;
  border-radius: 60px;
  box-shadow: 0px 0px 100px 25px rgb(100, 100, 96);
}

@media screen and (max-width: 1350px) {
  .profilePic {
    width: 10rem;
    height: 20rem
  }

  .aboutBody {
    width: 25rem
  }

  .techStackList {
    margin-bottom: 10rem;
  }
}

@media screen and (max-width: 800px) {
  .profilePic {
    width: 7rem;
    height: 12rem;
  }
}

.aboutSection {
  display: flex;
  justify-content: space-between;
  height: auto;
}

@media screen and (max-width: 1000px) {
  .aboutSection {
    display: flex;
    flex-direction: column;
  }

  .about-text-wrapper {
    text-align: left;
    width: 25rem;
  }

  .aboutMe {
    height: auto
  }

  .techStackList {
    position: relative;
    left: 0;
  }

  .profilePicContainer {
    display: flex;
    justify-content: center;
  }

}

.techStack {
  color: rgb(3, 238, 172);
  position: relative;
  left: 12rem;
}

/* ul.techList {
  list-style-image: url();
} */

.techStackList {
  width: 40vw;
  display: flex;
  justify-content: center;
}